import React, { Component, useState } from 'react';
// import { BrowserRouter as Router, Switch, 
//   Route, Redirect,} from "react-router-dom";
import { StaticQuery, graphql } from "gatsby"
import CallGif from "../images/call-icon.gif"
import { Nav, Navbar, NavDropdown, Modal, Button } from 'react-bootstrap';
import $ from "jquery";



export default function Video () {
  const [show, setShow] = useState(false);

  const handleClose = () => setShow(false);
  const handleShow = () => setShow(true);
  const fnSendMeDetail = (form) => {
    var project = "test";
    var name = $("#" + form + " input[name=name]").val();
    var email = $("#" + form + " input[name=email]").val();
    var mobile_no = $("#" + form + " input[name=mobile]").val();

    var email_pattern = /^\w+([-+.'][^\s]\w+)*@\w+([-.]\w+)*\.\w+([-.]\w+)*$/;
    var mobile_length = mobile_no.length;

    if (name == "") {
      $("#" + form + " .form_error").html("Please enter name");
      return false;
    } else {
      $("#" + form + " .form_error").html("");
    }

    if (email != "") {
      if (!email_pattern.test(email)) {
        $("#" + form + " .form_error").html("Please enter valid email");
        return false;
      } else {
        $("#" + form + " .form_error").html("");
      }
    }

    if (mobile_no == "") {
      $("#" + form + " .form_error").html("Please enter mobile no.");
      return false;
    } else if (mobile_length != 10) {
      $("#" + form + " .form_error").html("Please enter valid mobile no.");
      return false;
    } else {
      $("#" + form + " .form_error").html("");
    }

    $('#' + form + ' button').prop('disabled', true);

    var lead = {
      name: name,
      email: email,
      mobile: mobile_no,
      project: project,
      project_id: 11111
    };

    $.ajax({
      type: "post",
      url: 'https://mls-crm.manishalifespaces.com/api/add_lead',
      data: lead,
      success: function (data, status, jqXHR) {
        window.location.href = 'thanks';
      },
      error: function (jqXHR, status) {
        $('#' + form + ' button').prop('disabled', false);
      }
    });
  }
  return (
    <StaticQuery
      query = {graphql`
      query Navbar {
       allWpPost(filter: {uri: {}, slug: {eq: "logo"}}) {
          nodes {
            slug
            content
            featuredImage {
              node {
                sourceUrl
              }
            }
          }
        }
       devLogo:allWpPost(filter: {uri: {}, slug: {eq: "dev-logo"}}) {
          nodes {
            slug
            content
            featuredImage {
              node {
                sourceUrl
              }
            }
          }
        }
      }
      `}
      render = { data => (
    

    <><nav class="navbar navbar-expand-xl shadow-sm bg-white fixed-top p-0">
          <div class="container-fluid"> <button class="navbar-toggler collapsed" type="button" data-bs-toggle="collapse" data-bs-target="#collapsibleNavbar"> <span class="navbar-toggler-icon"></span> </button> <a class="navbar-brand" href="./">
            <img class="navbar-logo" src={data.allWpPost.nodes[0].featuredImage.node.sourceUrl} />
          </a>
            <div class="call-icon d-block d-xl-none"> <a href="tel:+918657449088"><img src={CallGif} alt="JP Dream Homes" /></a> </div>
            <div class="collapse navbar-collapse pe-md-3" id="collapsibleNavbar">
              <div class="row">
                <div class="col-12 d-none d-xl-block">
                  <div class="contact-number"> <a href="tel:+918657449088">+918657449088</a></div>
                </div>
                <div class="col-12">
                <Navbar bg="white" expand="lg">
        <Navbar.Toggle aria-controls="basic-navbar-nav" />
        <Navbar.Collapse id="basic-navbar-nav">
          <Nav className="me-auto">
            <Nav.Link className='py-0 px-md-3 px-2' href="./#overview">Overview</Nav.Link>
            <Nav.Link className='py-0 px-md-3 px-2' href="./#amenities">Amenities</Nav.Link>
            <Nav.Link className='py-0 px-md-3 px-2' href="./#gallery">Gallery</Nav.Link>
            <Nav.Link className='py-0 px-md-3 px-2' href="./#location">Location</Nav.Link>
            <Nav.Link className='py-0 px-md-3 px-2' href="./#plans">Plans</Nav.Link>
            <Nav.Link className='py-0 px-md-3 px-2' href="./#price">Price List</Nav.Link>
            <Nav.Link className='py-0 px-md-3 px-2' href="./#offer">Offer</Nav.Link>
            <Nav.Link className='py-0 px-md-3 px-2' href="javascript:void(0);" onClick={handleShow}>Site Visit</Nav.Link>
            <NavDropdown title="More" id="basic-nav-dropdown">
              <NavDropdown.Item href="./#highlights">Highlights</NavDropdown.Item>
              <NavDropdown.Divider />
              <NavDropdown.Item href="./#video">
              Video Walk Through
              </NavDropdown.Item>
              <NavDropdown.Divider />
              <NavDropdown.Item href="./#contact-sales">Contact Sales</NavDropdown.Item>
            </NavDropdown>
          </Nav>
        </Navbar.Collapse>
    </Navbar>
                  {/* <ul class="navbar-nav">
                    <li class="nav-item"> <a class="nav-link py-0 px-md-3 px-2" href="./#overview">Overview</a> </li>
                    <li class="nav-item"> <a class="nav-link py-0 px-md-3 px-2" href="./#amenities">Amenities</a> </li>
                    <li class="nav-item"> <a class="nav-link py-0 px-md-3 px-2" href="./#gallery">Gallery</a> </li>
                    <li class="nav-item"> <a class="nav-link py-0 px-md-3 px-2" href="./#location">Location</a> </li>
                    <li class="nav-item"> <a class="nav-link py-0 px-md-3 px-2" href="./#plan">Plans</a> </li>
                    <li class="nav-item"> <a class="nav-link py-0 px-md-3 px-2" href="./#price">Price List</a> </li>
                    <li class="nav-item"> <a class="nav-link py-0 px-md-3 px-2" href="./#offer">Offer</a> </li>
                    <li class="nav-item"> <a class="nav-link py-0 px-md-3 px-2" href="javascript:void(0);" onClick={handleShow}>Site Visit</a> </li>
                    <li class="nav-item"> <a class="nav-link py-0 px-md-3 px-2" href="./nri">NRI Services</a> </li>
                    <li class="nav-item dropdown border-end-0"> <a class="nav-link py-0 px-3 pe-0 dropdown-toggle" href="#" role="button" data-bs-toggle="dropdown">More</a>
                      <ul class="dropdown-menu text-center">
                        <li><a class="dropdown-item" href="./#highlights">Highlights</a></li>
                        <li><a class="dropdown-item" href="./#video">Video Walk Through</a></li>
                        <li><a class="dropdown-item" href="./#contact-sales">Contact Sales</a></li>
                      </ul>
                    </li>
                  </ul> */}
                </div>
              </div>
            </div>
            <div class="developer-logo d-none d-xl-block"> <a href="https://passcodehighway.com/" id="site-logo" title="Adhiraj" rel="home"><img src={data.devLogo.nodes[0].featuredImage.node.sourceUrl} alt="Lodha Kolshet" /></a> </div>
          </div>
        </nav><Modal show={show} onHide={handleClose} centered>
            <Modal.Body>
              <Button variant="primary" onClick={handleClose} style={{ float: 'right' }}>X</Button>
              <img style={{ width: "100%", padding: "0rem 5rem", margin: "auto" }} src={data.allWpPost.nodes[0].featuredImage.node.sourceUrl} />
              <h4 class="text-center text-white pt-5 pb-4">Fill details for current offers</h4>
              <form id="sitePopupForm" class="px-4">
                <div class="mb-3"> <input type="text" class="form-control" name="name" placeholder="Name" /> </div>
                <div class="mb-3"> <input type="email" class="form-control" name="email" placeholder="Email" /> </div>
                <div class="mb-3"> <input type="number" class="form-control mobile" name="mobile" placeholder="Mobile No." /> </div>
                <div class="form-check mb-3"> <input class="form-check-input" type="checkbox" value="" id="site-form-check" /> <label class="form-check-label text-white" for="site-form-check"> Yes, I want quick call back </label> </div>
                <p class="text-danger form_error"></p><button type="button" class="btn btn-primary shadow-lg w-100 mb-5" onClick={() => fnSendMeDetail("sitePopupForm")}>SEND ME DETAILS</button> </form>
            </Modal.Body>
          </Modal></>

)}
/>
)
}